import React, {Component} from 'react';
import {arrayFlashCardData} from "../data/array";
import { ObjectMethods } from '../data/object';
import Card from './Card';
import CreateCard from './CreateCard';
import _ from "lodash";

const styles = {
  cardContainer: {
    width: '100%',
  },
};


class CardContainer extends React.Component {
  constructor() {
    super();
    this.state = {
      cards: arrayFlashCardData.sort(() => Math.random() - 0.5),
      cardNumber: 0,
      cardLength: '',
    };
    this.boundCreateCard = this.setCard.bind(this);
    this.boundShowPrevCard = this.showPrevCard.bind(this);
    this.boundShowNextCard = this.showNextCard.bind(this);
  }

  componentDidMount() {
    this.selectionLogic();
  }

  componentDidUpdate( prevProps ) {
    if ( prevProps.flashCardStack !== this.props.flashCardStack ) {
      this.selectionLogic();
    }
  }


  selectionLogic = () => {
    if ( this.props.flashCardStack === 'Arrays' ) {
      this.setState({
        cards: arrayFlashCardData.sort(() => Math.random() - 0.5),
        cardNumber: 0,
        cardLength: arrayFlashCardData.length
      })
    }


    if ( this.props.flashCardStack === 'Objects' ) {
      this.setState({
        cards: ObjectMethods.sort(() => Math.random() - 0.5),
        cardNumber: 0,
        cardLength: ObjectMethods.length,
      })
    }
  };

  hideCreateCard = () => {
    this.setState({showModal: false});
  };

  showNextCard() {
    if (( this.state.cardNumber + 1) !== this.state.cardLength ) {
      this.setState({cardNumber: this.state.cardNumber += 1});
    }

    // if( this.state.cardNumber + 1 === this.state.cardLength ) {
    //   this.setState({ cardNumber: 0 });
    // }
  }

  showPrevCard() {
    if (this.state.cardNumber !== 0) {
      this.setState({cardNumber: this.state.cardNumber -= 1});
    }
  }

  setCard = (card) => {
    const newCards = this.state.cards.push(card);
    this.setState({cards: newCards});
  };

  generateDots() {
    const times = this.state.cards.size;
    let arr = [];
    _.times(times).forEach((num) => {
      const dotClass = num === this.state.cardNumber ? 'active' : '';
      arr.push(
        <span
          className={`card-container__dot fa fa-circle ${dotClass}`}
          onClick={() => this.setState({cardNumber: num})}
        />
      )
    });
    return arr;
  }

  generateCards() {
    const cards = this.state.cards;
    const cardsList = cards.map((card) => {
      return (
        <Card
          frontContent={card.question}
          backContent={card.description}
          code={card.code}
          total={cards.length}
          destructive={card.destructive}
          showNextCard={this.boundShowNextCard}
          showPrevCard={this.boundShowPrevCard}
          cardNumber={this.state.cardNumber}
        />
      );
    })
    return cardsList[this.state.cardNumber];
  }

  render() {
    console.log('card container state:  ', this.state);
    return (
      <div style={ styles.cardContainer } className="card-container">
                <span
                  className='card-container__icon  fa fa-plus'
                  onClick={() => this.setState({showModal: !this.state.showModal})}
                />
        {
          this.state.showModal
            ? <CreateCard
              onShadowClick={this.hideCreateCard}
              onCreateCard={this.setCard}
            />
            : ''
        }
        {this.generateCards()}
        <div className='card-container__dots-wrapper'>
          {this.generateDots()}
        </div>
      </div>
    );
  }
}

export default CardContainer;